import {
	createRouter,
	createWebHistory
} from 'vue-router'
import IndexView from '../views/IndexView.vue'
import RuleView from '../views/RuleView.vue'
import RoomView from '../views/RoomView.vue'
import GameView from '../views/GameView.vue'
import NotFound from '../views/NotFound.vue'
// import AboutView from '../views/AboutView.vue'
import ChatBox from '../components/ChatBox.vue'
import LoginView from '../views/LoginView.vue'

const routes = [{
		path: '/',
		name: 'index',
		component: IndexView,
		// 临时测试首页
		// redirect: '/chatbox',
	},
	{
		path: '/index',
		redirect: '/'
	},
	{
		path: '/rule',
		name: 'rule',
		component: RuleView,
		children: [{
				path: 'play',
				component: RuleView
			},
			{
				path: 'type',
				component: () => import('../views/NotFound.vue')
			}
		]
	},
	{
		path: '/room',
		name: 'room',
		component: RoomView
	},
	{
		path: '/game',
		name: 'game',
		component: GameView,
	},
	{
		path: '/chatbox',
		name: 'chatbox',
		component: ChatBox,
	},
	{
		path: '/login',
		name: 'login',
		component: LoginView,
	},

	// {
	// 	path: '/about',
	// 	name: 'about',
	// 	component: AboutView
	// },

	// {
	// 	path: '/home',
	// 	name: 'home',
	// 	hidden: true,
	// 	redirect: '/',
	// 	component: HomeView,
	// 	// children: [
	// 	// 	{
	// 	// 		path: '',
	// 	// 		component: () => import('../views/')
	// 	// 	}
	// 	// ]
	// },
	{
		// 404页面
		// 使用正则表达式，匹配任意内容，正则前面要用冒号:形式
		path: '/:path(.*)',
		component: NotFound,
	},
]

const router = createRouter({
	history: createWebHistory(process.env.BASE_URL),
	routes,
	linkActiveClass: 'light'
})

export default router