<template>
	<el-card class="index" id="index" shadow="never">
		<template #header>
			<div class="home_header">
				<el-row :gutter="20" justify="space-between" align="middle">
					<!-- 用户头像 -->
					<el-col :span="3">
						<el-dropdown popper-class="popper_select" :popper-append-to-body="false">
							<el-button>
								<el-avatar shape="square" :size="30" :src="avatarPic" loading />
							</el-button>
							<template #dropdown>
								<el-dropdown-menu>
									<el-dropdown-item>
										<el-icon>
											<Edit />
										</el-icon>编辑
									</el-dropdown-item>
									<el-dropdown-item divided disabled>
										<el-icon>
											<SwitchButton />
										</el-icon>退出
									</el-dropdown-item>
								</el-dropdown-menu>
							</template>
						</el-dropdown>
					</el-col>
					<el-col :span="5"></el-col>
					<!-- 标题内容 -->
					<el-col :span="8">
						<div style="font-weight: bold;font-size: 24px;">{{title}}</div>
					</el-col>
					<el-col :span="5"></el-col>
					<!-- 右侧额外 -->
					<el-col :span="3">
						<el-button plain>设置</el-button>
					</el-col>
				</el-row>
			</div>
		</template>
		<el-space direction="horizontal" alignment="center" :fill="true">
			<!-- 人数选择 -->
			<el-row :gutter="20" justify="space-between" align="middle">
				<el-col :span="24">
					<el-radio-group v-model="$store.state.content_form[0].select" size="small">
						<el-radio-button v-for="item,index in $store.state.content_form[0].list" :key="index"
							:label='index+1'>{{item}}</el-radio-button>
					</el-radio-group>
				</el-col>
			</el-row>
			<!-- 当前模式 -->
			<el-row :gutter="20" justify="space-between" align="middle">
				<el-col :span="24">
					<el-badge class="current_badge"	:value="$store.state.content_form[1].list[$store.state.content_form[1].select-1]">
						<el-button type="success" plain round size="default">
							{{$store.state.content_form[$store.state.content_form[1].select+1].list[$store.state.content_form[$store.state.content_form[1].select+1].select-1]}}
						</el-button>
					</el-badge>
				</el-col>
			</el-row>
			<!-- 规则设置 -->
			<el-row :gutter="20" justify="space-between" align="middle">
				<el-col :span="24">
					<el-button type="primary" plain size="large" @click="rule">规则设置</el-button>
				</el-col>
			</el-row>
			<!-- 创建房间 -->
			<el-row :gutter="20" justify="space-between" align="middle">
				<el-col :span="24">
					<el-button type="warning" plain size="large" @click="room">创建房间</el-button>
				</el-col>
			</el-row>
			<!-- 开始匹配 -->
			<el-row :gutter="20" justify="space-between" align="middle">
				<el-col :span="24">
					<el-button type="success" plain size="large" @click="game">开始匹配</el-button>
				</el-col>
			</el-row>
			<el-divider></el-divider>
			<div id="bottom_message">
				<el-card shadow="never" body-style="padding: 0px;">
					<el-row :gutter="20" justify="space-between" align="middle" style="font-size: 500;">
						<el-col :span="4"></el-col>
						<el-col :span="4">width:{{width}}</el-col>
						<el-col :span="4">height:{{height}}</el-col>
						<el-col :span="8">device:{{type}}</el-col>
						<el-col :span="4"></el-col>
					</el-row>
				</el-card>
			</div>
		</el-space>
	</el-card>
</template>

<script>
	import {
		ref
	} from 'vue'

	export default {
		name: 'LoginView',
		components: {},
		data() {
			return {
				title: '自由扑克',
				sizeList: ['small', 'default', 'large'],
				avatarPic: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',
				height: ref(0),
				width: ref(0),
				type: ref(0),
			}
		},
		methods: {
			rule() {
				this.$router.push({
					path: '/rule'
				})
			},
			room() {
				this.$router.push({
					path: '/room'
				})
			},
			game() {
				this.$router.push({
					path: '/game'
				})
			},
			init() {
				this.updatescreen();
				this.onWindowSizeChanged();
			},
			updatescreen() {
				var html = document.getElementsByTagName('html')[0];
				var width = html.clientWidth;
				var height = html.clientHeight;
				var max = width > height ? width : height;
				var min = width > height ? height : width;
				this.height = min;
				this.width = max;
			},
			onWindowSizeChanged() {
				window.addEventListener("resize", this.updatescreen)
			},
			getuseragent() {
				var ua = navigator.userAgent;
				console.log(ua);
				var android = /android[\s\d.]+(?:;)/ig;
				var apple = /ip[\w\s.]*(?:;)/ig;
				var windows = /windows[\w\s.]+(?:;)/ig;
				var mobile = /;[\w\s.]*build/ig;
				var type = ''

				if (ua.indexOf('Android') > -1) {
					console.log('安卓版本：', ua.match(android)[0].slice(0, -1));
					console.log('手机型号：', ua.match(mobile)[0].slice(2, -6));
					type = ua.match(android)[0].slice(0, -1) + "_" + ua.match(mobile)[0].slice(2, -6)
				} else if (ua.indexOf('iP') > -1) {
					console.log('苹果版本：', ua.match(apple)[0].slice(0, -1));
					type = ua.match(apple)[0].slice(0, -1)
				} else if (ua.indexOf('Windows') > -1) {
					console.log('电脑版本：', ua.match(windows)[0].slice(0, -1));
					type = ua.match(windows)[0].slice(0, -1)
				} else {
					console.log('设备版本：', 'Unknown')
					type = 'Unknown'
				}
				this.type = type
			},
			get_current_msg() {
				this.$store.commit('add_once')
			}
		},
		mounted() {
			this.init();
			this.getuseragent();
		}
	}
</script>

<style>
	.el-card__body {
		height: 100%;
	}

	#index {
		height: 99.5vh;
	}

	#bottom_message {
		font-size: x-small;
	}

	.popper_select {
		position: absolute !important;
		top: 60px !important;
		left: 15px !important;
	}

	/* 	.current_badge .el-badge__content{
		position: absolute;
		top:10px;
	} */

	.el-badge__content.is-fixed {
		position: absolute !important;
		top: 10px !important;
	}
</style>