<template>
	<div id='chatbox'>
		<div class="chatwindow">
			聊天窗
			<div class="chatlist" style="border: 1px solid #000;height: 280px;overflow: auto;">
				<ul class="msg_ul">
					<li class="msg_li" v-for="item in msgList" :key="item.id">
						<div :class="{showmsg_me:item.user==username,showmsg_other:item.user!=username}">
							<!-- 用户头像 -->
							<div class="msg_avatar">
								<el-avatar shape="square" :size="36" :src="randAvatar" />
							</div>
							<!-- 消息盒子 -->
							<div :class="{msg_box_me:item.user==username,msg_box:item.user!=username}">
								<!-- 消息信息 -->
								<div :class="{msg_info_me:item.user==username,msg_info:item.user!=username}">
									<!-- 用户名 -->
									<span class="msg_name">{{item.user}}</span>
									<!-- 时间 -->
									<span class="msg_date">{{item.dateTime}}</span>
								</div>
								<!-- 消息内容 -->
								<div
									:class="{msg_wrapper_me:item.user==username,msg_wrapper_other:item.user!=username}">
									<!-- 气泡 -->
									<span
										:class="{msg_bubble_me:item.user==username,msg_bubble_other:item.user!=username}">
										<!-- 文字 -->
										<span>{{item.msg}}</span>
									</span>
									<!-- 更多操作 -->
									<el-popconfirm width="100" confirm-button-text="确定" cancel-button-text="取消"
										icon="InfoFilled" icon-color="#626AEF" title="撤回消息？" :hide-after='0'
										@confirm="pullback(item.id),msg_sure_back()" @cancel='msg_cancle_back()'
										v-if="item.user==username">
										<template #reference>
											<span class="msg_more">
												<el-button text plain style="padding: 5px;margin-bottom: 5px;">
													<el-icon>
														<More />
													</el-icon>
												</el-button>
											</span>
										</template>
									</el-popconfirm>
								</div>
							</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
		<el-input type="textarea" v-model="msg" :rows="2" @keypress.enter.prevent="sendMessage(),scroll_bottom()"
			placeholder="请输入聊天内容" autofocus>

		</el-input>
		<el-button type="primary" @click="sendMessage(),scroll_bottom()">发送</el-button>
	</div>
</template>

<script>
	import {
		onMounted,
		reactive,
		ref,
		toRefs,
	} from 'vue'
	import {
		useRouter
	} from 'vue-router';
	import {
		useWebSocket
	} from '../composition';
	import {
		ElMessage
	} from 'element-plus'

	export default {
		name: 'ChatBox',
		data() {
			return {
				randAvatar: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',
				username: sessionStorage.getItem('username'),
			}
		},
		setup() {
			const router = useRouter();
			const ws = useWebSocket(handleMessage)
			var state = reactive({
				msgList: ref([]),
				msg: '',
			})
			// 获取历史记录
			state.msgList = JSON.parse(localStorage.getItem('bk_chat_history')) || [];

			let username = ''
			onMounted(() => {
				username = sessionStorage.getItem('username')
				if (!username) {
					router.push({
						path: '/login'
					})
					return;
				}
			})

			// 发送消息
			const sendMessage = () => {
				const _msg = state.msg
				console.log(_msg);
				if (!_msg.trim().length) {
					// this.msg_blank_warning
					ElMessage({
						message: '消息内容不能为空',
						type: 'warning',
						duration: 800,
					})
					state.msg = ''
					return;
				}
				// state.msgList.push({
				// 	id: new Date().toLocaleString(),
				// 	user: username,
				// 	dateTime: new Date().getTime(),
				// 	msg: state.msg,
				// })

				ws.send(JSON.stringify({
					id: new Date().getTime(),
					user: username,
					dateTime: new Date().toLocaleString(),
					msg: state.msg,
				}))
				state.msg = ''

			}

			// 处理接受消息
			function handleMessage(e) {
				// console.log('WebSocket原数据', e);
				const _msgdata = JSON.parse(e.data);
				console.log('发送数据', JSON.parse(e.data));

				state.msgList.push(_msgdata);
				localStorage.setItem('bk_chat_history', JSON.stringify(state.msgList))
			}
			// console.log(ws);

			// 撤回消息
			function pullback(id) {
				console.log(id);
				var s = state.msgList.map(item => (item.id))

				if (s.indexOf(id) != -1) {
					state.msgList.splice(s.indexOf(id), 1)
				}

				localStorage.setItem('bk_chat_history', JSON.stringify(state.msgList))
			}

			return {
				...toRefs(state),
				sendMessage,
				pullback
			}
		},
		mounted() {
			this.scroll_bottom()
		},
		updated() {
			this.scroll_bottom()
		},
		methods: {
			msg_cancle_back() {
				// this.$message.success(text: '这是消息内容'),
				this.$message({
					message: '取消撤销操作',
					type: 'info', // success(成功)/warning(警告)/info(消息)/error(错误)/;
					icon: 'CircleCloseFilled',
					// dangerouslyUseHTMLString:false,//是否将 message 属性作为 HTML 片段处理	
					// customClass:'',//自定义类名	
					duration: 1000, //显示时间，单位为毫秒。 设为 0 则不会自动关闭	
					// showClose,//是否显示关闭按钮	
					// center,//文字是否居中	
					// onClose,//关闭时的回调函数, 参数为被关闭的 message 实例	
					// offset,//Message 距离窗口顶部的偏移量	
					// appendTo,//设置 message 的根元素，默认为 document.body	
					// grouping:false,//合并内容相同的消息，不支持 VNode 类型的消息	
					// repeatNum:1,//重复次数，类似于 Badge 。当和 grouping 属性一起使用时作为初始数量使用
				})
			},
			msg_sure_back() {
				this.$message({
					message: '撤回消息成功',
					type: 'success',
					duration: 1000
				})
			},
			// msg_blank_warning() {
			// 	this.$message({
			// 		message: '消息内容不能为空',
			// 		type: 'warning',
			// 		duration: 600
			// 	})
			// },
			scroll_bottom() {
				setTimeout(() => {
					this.$nextTick(() => {
						// this.$refs.chatlist.scrollTop = this.$refs.msg_ul.scrollHeight;
						// let scrollEl = this.$refs.mianscroll;
						// console.log(this.$refs);
						// scrollEl.scrollTo({
						// 	top: scrollEl.scrollHeight,
						// 	behavior: 'smooth'
						// });
						var el_chatlist = this.$el.querySelector(".chatlist")
						el_chatlist.scrollTop = el_chatlist.scrollHeight;
					})
				}, 20)

			}
		}
	}
</script>

<style>
	.showmsg_other {
		display: flex;
		justify-content: start;
		align-items: flex-start;
	}

	.showmsg_me {
		display: flex;
		justify-content: end;
		align-items: flex-start;
		flex-direction: row-reverse;
	}

	.msg_li {
		margin: 10px;
		align-self: left;
	}

	.msg_avatar {
		border-radius: 5px;
	}

	.msg_box {
		display: block;
		text-align: left;
	}

	.msg_box_me {
		display: block;
		text-align: right;
	}

	.msg_info {
		display: flex;
		flex-direction: row;
		justify-content: start;
	}

	.msg_info_me {
		display: flex;
		justify-content: end;
		align-items: end;
		flex-direction: row;
	}

	.msg_name {
		margin: 0px 5px 0px 5px;
		color: #ccc;
		font-size: 14px;
	}

	.msg_date {
		margin: 0px 5px 0px 5px;
		color: #ddd;
		font-size: 14px;
	}

	.msg_wrapper_other {
		display: flex;
		flex-direction: row;
	}

	.msg_wrapper_me {
		display: flex;
		flex-direction: row-reverse;
	}

	.msg_bubble_other {
		background-color: #81baff;
		color: #000;
		max-width: 280px;
		min-width: 20px;
		border-radius: 5px;
		text-align: left;
		display: inline-flex;
		flex-direction: column;
		justify-content: left;
		align-items: start;
		margin: 2px 5px 5px 5px;
		padding: 10px;
	}

	.msg_bubble_me {
		background-color: #cee4ff;
		color: #000;
		max-width: 280px;
		min-width: 20px;
		border-radius: 5px;
		text-align: left;
		display: inline-block;
		flex-direction: row-reverse;
		justify-content: left;
		align-items: start;
		margin: 2px 5px 5px 5px;
		padding: 10px;
	}

	.msg_more {
		margin: 0px 0px 0px 0px;
		display: flex;
		justify-content: end;
		align-items: flex-end;
	}
</style>