<template>
	<div id="game">
		<div class="game_wrapper">
			<el-row :gutter="4" justify="space-between" align='middle' style="height: 100px;">
				<!-- 左侧用户 -->
				<el-col :span="4">
					<div class="" style="background-color: #c91c9100;">
						<!-- 用户头像 -->
						<div class="user_avatar">
							<el-avatar shape="square" :size="36" :src="randAvatar" />
						</div>
						<!-- 用户名 -->
						<div class="user_name">
							<el-text class="user_text" type="info">admin</el-text>
						</div>
						<!-- 积分 -->
						<div class="user_score" style="color: #98FCFE;">
							<el-text class="user_text" tag="span"><el-icon>
									<Coin />
								</el-icon>456</el-text>
						</div>
					</div>
				</el-col>
				<!-- 左侧牌背面 -->
				<el-col :span="4" class="user_card_wrapper">
					<div class="user_card">{{card_num_list[0]}}</div>
				</el-col>
				<el-col :span="8">
					<div class="" style="background-color: #c91c91;height: 100px;">东</div>
				</el-col>
				<!-- 右侧牌背面 -->
				<el-col :span="4" class="user_card_wrapper">
					<div class="user_card">{{card_num_list[1]}}</div>
				</el-col>
				<!-- 右侧用户 -->
				<el-col :span="4">
					<div class="" style="background-color: #c91c9100;">
						<!-- 用户头像 -->
						<div class="user_avatar">
							<el-avatar shape="square" :size="36" :src="randAvatar" />
						</div>
						<!-- 用户名 -->
						<div class="user_name">
							<el-text class="user_text" type="info">admin</el-text>
						</div>
						<!-- 积分 -->
						<div class="user_score" style="color: #98FCFE;">
							<el-text class="user_text" tag="span"><el-icon>
									<Coin />
								</el-icon>789</el-text>
						</div>
					</div>
				</el-col>
			</el-row>
			<!-- 出牌区 -->
			<el-row :gutter="4" justify="space-between" align='middle' style="height: 130px;">
				<el-col :span="4">
					<div class="" style="background-color: #c91c91;height: 130px;">催下</div>
				</el-col>
				<el-col :span="16">
					<div class="" style="background-color: #c91c91;height: 130px;">催下</div>
				</el-col>
				<el-col :span="4">
					<div class="" style="background-color: #c91c91;height: 130px;">东</div>
				</el-col>
			</el-row>
			<!-- 出牌操作模块 -->
			<el-row :gutter="4" justify="space-between" align='middle' style="height: 40px;">
				<el-col :span="24">
					<div class="" style="background-color: #adc99600;height: 40px;line-height: 40px;">
						<el-button @click="select_not_all()" round type="info">全不选</el-button>
						<el-button @click="select_pass()" round type="danger">不出</el-button>
						<el-button @click="select_tips()" round type="warning">提示</el-button>
						<el-button @click="select_send()" round type="primary"
							:disabled='card_select_flag.length==0'>出牌</el-button>
					</div>
				</el-col>
			</el-row>
			<!-- 当前用户模块 -->
			<el-row :gutter="4" justify="space-between" align='middle' style="height: 110px;">
				<!-- 当前用户 -->
				<el-col :span="3">
					<div class="" style="background-color: #c91c9100;">
						<!-- 用户头像 -->
						<div class="user_avatar">
							<el-avatar shape="square" :size="36" :src="randAvatar" />
						</div>
						<!-- 用户名 -->
						<div class="user_name">
							<el-text class="user_text" type="info">我</el-text>
						</div>
						<!-- 积分 -->
						<div class="user_score">
							<el-text class="user_text" tag="span" style="color: #019dfe;"><el-icon>
									<Coin />
								</el-icon>1638</el-text>
						</div>
					</div>
				</el-col>
				<!-- 牌面区 -->
				<el-col :span="19">
					<div class="" style="background-color: #c91c9100;height: 110px;">
						<ul class="card_list">
							<li v-for="(item,index) in 18" :key="index"
								:class="{card_select:index==card_select_judge(index)}"
								@mousedown="select_mousedown(index)" @mouseenter="select_mouseenter(index)"
								@mouseup="select_mouseup()">
								<div class="card_list_show">
									<span class="card_list_num">{{item}}</span>
									<span class="card_list_icon">♣</span>
								</div>
							</li>
						</ul>
					</div>
				</el-col>
				<!-- 设置区 -->
				<el-col :span="2">
					<div class="" style="background-color: #c91c9100;height: 110px;">
						<el-row :gutter="0" justify="space-between" align='middle' style="height: 40px;">
						</el-row>
						<!-- 记牌器 -->
						<el-row :gutter="0" justify="space-between" align='middle' style="height: 30px;">
							<el-col :span="24">
								<el-button class="card_recorder" @click="card_recorder(),dialog_recorder=true"
									type="primary" plain size="small">记牌器</el-button>
							</el-col>
						</el-row>
						<!-- 设置按钮 -->
						<el-row :gutter="0" justify="space-between" align='middle' style="height: 40px;">
							<el-col :span="24">
								<el-button class="card_set" @click="card_set()" type="info" plain round>
									<el-icon size="20">
										<Setting />
									</el-icon>
								</el-button>
							</el-col>
						</el-row>
					</div>
				</el-col>
			</el-row>
		</div>
		<!-- 模态框 -->
		<el-dialog v-model="dialog_recorder" title="记牌器" width="30%">
			<span>This is a message</span>
			<template #footer>
				<span class="dialog-footer">
					<el-button @click="dialog_recorder = false">取消</el-button>
					<el-button type="primary" @click="dialog_recorder = false">
						确定
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>

<script>
	import {
		ref
	} from 'vue';
	export default {
		name: 'GameView',
		components: {},
		data() {
			return {
				randAvatar: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',
				card_num_list: [9, 14],
				card_select_flag: ref([]),
				dialog_recorder: false,
				select_mousedown_flag: ref(0)
			}
		},
		methods: {
			index() {
				this.$router.push({
					path: '/index'
				})
			},
			select_card(index) {
				// console.log('选择了', this);
				// 选择列表里面有了这张牌
				if (this.card_select_flag.indexOf(index) != -1) {
					// 移除选择标记
					this.card_select_flag.splice(this.card_select_flag.indexOf(index), 1)
				} else {
					this.card_select_flag.push(index)
				}
			},
			card_select_judge(index) {
				if (this.card_select_flag.indexOf(index) != -1) {
					return index;
				}
			},
			// **********************************出牌操作**********************************
			// 全不选
			select_not_all() {
				this.card_select_flag = ref([]);
			},
			// 不出
			select_pass() {
				this.select_not_all()
			},
			// 提示
			select_tips() {
				this.$message({
					message: '待开发……',
					type: 'warning',
					grouping: true,
					duration: 500
				})
			},
			// 出牌
			select_send() {
				this.$message({
					message: '已出牌：' + this.card_select_flag,
					type: 'success',
					grouping: true,
					duration: 1100
				})
			},
			// 记牌器
			card_recorder() {
				this.$message({
					message: '记牌器',
					type: 'success',
					grouping: true,
					duration: 500
				})
			},
			// 设置
			card_set() {
				this.$message({
					message: '设置',
					type: 'success',
					grouping: true,
					duration: 500
				})
			},

			// **********************************出牌操作**********************************
			select_mousedown(index) {
				this.select_mousedown_flag = 1
				console.log(index);
				this.select_card(index)
			},
			select_mouseenter(index) {
				if (this.select_mousedown_flag == 1) {
					console.log(index);
					this.select_card(index)
				}
			},
			select_mouseup() {
				this.select_mousedown_flag = 0
			},
		},
	}
</script>

<style>
	:root {
		--game_wrapper_padding: 5px;
		--card_size_width: 56px;
		--card_size_height: 70px;
	}

	.game_wrapper {
		width: auto;
		height: calc(390px - var(--game_wrapper_padding));
		padding: 4px var(--game_wrapper_padding);
		overflow: auto;
	}

	.el-row {
		/* margin-top: 5px; */
		/* margin-bottom: 5px; */
	}

	.user_card_wrapper {
		display: flex;
		align-items: center;
		justify-self: center;
	}

	.user_card {
		width: var(--card_size_width);
		height: var(--card_size_height);
		line-height: 70px;
		border: 1px solid #555;
		border-radius: 5px;
		font-size: 26px;
		display: inline-flex;
		justify-content: center;
	}

	.card_list {
		height: 105px;
		list-style: none;
		display: flex;
		justify-content: center;
		align-items: flex-end;
		user-select: none;
	}

	/* 首牌无样式 */
	.card_list li:nth-child(1) {
		margin-left: 0px;
	}

	.card_list li {
		width: var(--card_size_width);
		height: var(--card_size_height);
		border: 1px solid #555;
		margin-left: -25px;
		background-color: #fff;
		border-radius: 5px;
		text-align: left;
		transition: all 0.1s cubic-bezier(0.445, 0.05, 0.55, 0.95);
	}

	/* 选中的牌 */
	.card_select {
		margin-bottom: 25px;
	}

	.card_list_show {
		width: 20px;
		/* background-color: #2a6; */
		text-align: center;
	}

	.card_list_num {
		display: block;
		/* margin-top: 4px; */
		/* margin-left: 4px; */
	}

	.card_list_icon {
		display: block;
		width: 20px;
		height: 20px;
		/* margin-top: 0px; */
		/* margin-left: 4px; */
	}

	/* 记牌器 */
	.el-button.card_recorder {
		margin: 0px;
		padding: 0px 5px;
	}

	/* 设置 */
	.el-button.card_set {
		margin: 0px;
		padding: 0px;
	}
</style>