<template>
	<el-card class="rule" id="rule" shadow="never">
		<template #header>
			<el-page-header @back="goBack">
				<template #icon>
					<el-icon>
						<ArrowLeft />
					</el-icon>
				</template>
				<template #title>返回</template>
				<template #content><span>规则设置</span></template>
				<template #extra>
					<el-button type="primary" plain text icon="Warning">帮助</el-button>
					<el-button type="primary" icon="">保存</el-button>
				</template>
			</el-page-header>
		</template>
		<el-tabs type="border-card">
			<el-tab-pane label="出牌规则">
				<div class="playrule">
					<el-form label-position="left" label-width="100px" :model="$store.state.content_form"
						style="max-width: 460px">
						<!-- 对局人数 -->
						<el-form-item :label="$store.state.content_form[0].name">
							<el-radio-group v-model="$store.state.content_form[0].select">
								<el-radio-button v-for="item,index in $store.state.content_form[0].list" :key="index"
									:label="index+1" size="default">{{item}}</el-radio-button>
							</el-radio-group>
						</el-form-item>
						<!-- 使用规则 -->
						<el-form-item :label="$store.state.content_form[1].name">
							<el-radio-group v-model="$store.state.content_form[1].select">
								<el-radio-button v-for="item,index in $store.state.content_form[1].list" :key="index"
									:label="index+1" size="default">{{item}}</el-radio-button>
							</el-radio-group>
						</el-form-item>
						<el-divider />
						<!-- 预设规则 -->
						<el-form-item :label="$store.state.content_form[1].name"
							v-show="$store.state.content_form[1].select==1">
							<el-radio-group v-model="$store.state.content_form[2].select">
								<el-radio v-for="(item,index) in $store.state.content_form[2].list" :key='index'
									:label="index+1" border size="default">{{item}}</el-radio>
							</el-radio-group>
						</el-form-item>
						<!-- 自定义规则 -->
						<el-form-item :label="$store.state.content_form[3].name"
							v-show="$store.state.content_form[1].select==2">
							<el-radio-group v-model="$store.state.content_form[3].select">
								<el-radio v-for="(item,index) in $store.state.content_form[3].list" :key='index'
									:label="index+1" border size="default">{{item}}</el-radio>
								<el-button class="addrule_btn" type="primary" plain icon="Plus" size="default"
									@click="addRule($store.state.content_form[0].select)">添加</el-button>
							</el-radio-group>
						</el-form-item>
						<el-divider />
					</el-form>
				</div>
			</el-tab-pane>
			<el-tab-pane label="出牌类型">
				<div class="rulediv">
					<el-table :data="tableData" border><!-- style="width: 100%"> -->
						<el-table-column prop="data" label="类型" />
					</el-table>
				</div>
			</el-tab-pane>
			<el-tab-pane label="大小规则">
				<el-tabs :tab-position="tabposition" style="" type="border-card" stretch>
					<el-tab-pane label="User">
						<template #label>
							<el-space direction="horizontal" alignment="center" :fill="true">
								<el-row :gutter="20" justify="space-between" align="middle">
									<el-col :span="8">默认</el-col>
								</el-row>
							</el-space>
						</template>
					</el-tab-pane>
					<el-tab-pane label="Config">Config</el-tab-pane>
					<el-tab-pane label="Role">Role</el-tab-pane>
					<el-tab-pane label="Task">Task</el-tab-pane>
					<el-tab-pane label="Condds">Condds</el-tab-pane>
					<el-tab-pane label="Doirjm">Doirjm</el-tab-pane>
					<el-tab-pane label="Task1">Task</el-tab-pane>
					<el-tab-pane label="Condds2">Condds</el-tab-pane>
					<el-tab-pane label="Doirjm3">Doirjm</el-tab-pane>
				</el-tabs>
			</el-tab-pane>
		</el-tabs>
	</el-card>
</template>

<script>
	// import {ref} from 'vue'

	export default {
		name: 'RuleSetting',
		components: {},
		data() {
			return {
				tabposition: 'left',

				tableData: [{
						data: '单只'
					},
					{
						data: '对子'
					},
					{
						data: '三连只'
					},
					{
						data: '炸'
					},
					{
						data: '顺子'
					},
					{
						data: '三带一'
					},
					{
						data: '三带二'
					},
					{
						data: '四带一'
					},
					{
						data: '四带二'
					},
					{
						data: '王炸'
					},
				],
			}
		},
		methods: {
			goBack() {
				this.$router.push({
					path: '/index'
				})
			},
			addRule(useRuleType) {
				console.log(useRuleType);
			}

		},
		setup() {
			// 预设规则设置

		}
	}
</script>

<style>
	.el-tabs--border-card>.el-tabs__content {
		padding: 0px;
	}

	#rule .el-card__body {
		padding: 0px;
	}

	:root {
		--rule-padding: 15px;
	}

	.rulediv {
		border: 1px solid #E4E7ED;
		max-height: 240px;
		overflow: auto;
	}

	.playrule {
		border: 1px solid #E4E7ED;
		overflow: auto;
		max-height: calc(240px - var(--rule-padding)*2);
		padding: var(--rule-padding);
	}

	.el-radio-group>.el-radio,
	.addrule_btn {
		margin-right: 5px;
		margin-bottom: 6px;
	}
</style>