// import {
// 	WS_ADDRESS
// } from '../configs'
const WS_ADDRESS='ws://localhost:8080/freepoker/'

function useWebSocket(handleMessage) {
	const ws = new WebSocket(WS_ADDRESS);
	// 默认blob，修改为arraybuffer
	// ws.binaryType = "arraybuffer";
	const init = () => {
		bindEvent()
	}

	function bindEvent() {
		ws.addEventListener('open', handleOpen, false);
		ws.addEventListener('close', handleClose, false);
		ws.addEventListener('erroe', handleError, false);
		ws.addEventListener('message', handleMessage, false);
	}
	
	function handleOpen(){
		// console.log('[Success Opening] 服务器链接成功！',e);
		console.log('[Success Opening] 服务器链接成功！');
	}
	function handleClose(){
		// console.log('[Success Closing] 服务器断开成功！',e);
		console.log('[Success Closing] 服务器断开成功！');
	}
	function handleError(){
		// console.log('[Server Error] 服务器出错，请联系管理员！',e);
		console.log('[Server Error] 服务器出错，请联系管理员！');
	}

	init();

	return ws
}

export default useWebSocket;