<template>
	<div id="login">
		用户名：<el-input type="text" v-model="username" placeholder="请输入用户名"></el-input>
		密码：<el-input @keydown.enter="handleEnterBtnClick" type="password" v-model="password"
			placeholder="请输入密码"></el-input>
		<el-button type="primary" @click="handleEnterBtnClick">登录</el-button>
	</div>
</template>

<script>
	import {
		onMounted,
		ref,
	} from 'vue'
	import {
		useRouter
	} from 'vue-router';

	export default {
		name: 'LoginView',
		data() {
			return {}
		},
		setup() {
			const username = ref('');
			const password = ref('');
			const router = useRouter();
			onMounted(() => {
				username.value = sessionStorage.getItem('username')

				if (!username.value) {
					router.push({
						path: '/login'
					})
					return;
				}
			})

			const handleEnterBtnClick = () => {
				const _username = username.value.trim();
				console.log(_username);
				sessionStorage.setItem('username', _username);
				username.value = ''
				password.value = ''
				router.push({
					path: '/chatbox'
				})
			}

			return {
				handleEnterBtnClick,
				username,
				password
			}
		}
	}
</script>

